body {
  margin: 0;
  font-family: 'Roboto Condensed', sans-serif;
  background-color: #fafafa;
  color: #333;
  overflow-x: hidden; 
}

.topbar {
  width: 100%;
  background: #0066cc;
  color: white;
  padding: 15px 20px;
  font-size: 1.5rem;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  flex-wrap: wrap;
  overflow: hidden;
}

.topbar span {
  font-weight: bold;
}

.right{
  margin-right: 100px;
}

.topbar div {
  display: flex;
  gap: 15px; 
  flex-wrap: wrap; 
}

.topbar a {
  color: white;
  text-decoration: none;
  font-size: 1rem;
  padding: 8px 12px;
  background: #004c99;
  border-radius: 5px;
  transition: background 0.3s;
}

.topbar a:hover {
  background: #003366;
}

.container {
  margin-top: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  text-align: center;
  padding: 20px;
}

h1 {
  font-size: 3rem;
  font-weight: bold;
  margin: 0;
  color: #333;
  animation: fadeInUp 2s ease-out, colorChange 5s infinite;
}

p {
  font-size: 1.5rem;
  margin-top: 20px;
  animation: fadeIn 3s ease-out, colorChange 5s infinite;
}

.url-section-wrapper {
  margin-top: 20px;
}

.url-section {
  font-size: 1.2rem;
  color: #555;
  background: #f0f0f0;
  padding: 15px;
  border-radius: 5px;
  margin-bottom: 10px;
  width: 100%;
  max-width: 600px;
}

.url-section strong {
  color: #0066cc;
}

button {
  margin-top: 40px;
  padding: 12px 25px;
  font-size: 1.1rem;
  background: #ff7f7f;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
  transition: background 0.3s;
  font-family: 'Roboto Condensed', sans-serif;
  animation: buttonColorChange 5s infinite;
}

button:hover {
  background: #ff4d4d;
}

.footer {
  width: 100%;
  background: #333;
  color: white;
  padding: 10px 0;
  position: fixed;
  bottom: 0;
  left: 0;
  text-align: center;
  font-size: 1rem;
  cursor: pointer;
}

.footer a {
  color: white;
  text-decoration: none;
}

.footer:hover {
  text-decoration: none;
}

.scrollable-token-wrapper {
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  width: 100%;
  padding: 5px;
}

.scrollable-token {
  white-space: nowrap;
  padding: 10px;
  border-radius: 5px;
  font-size: 1.2rem;
  box-sizing: border-box;
  color: #0066cc;
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(-50px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes colorChange {
  0% {
    color: #0066cc;
  }
  25% {
    color: #ff7f7f;
  }
  50% {
    color: #ffcc00;
  }
  75% {
    color: #00cc66;
  }
  100% {
    color: #0066cc;
  }
}

@keyframes buttonColorChange {
  0% {
    background: #ff7f7f;
  }
  25% {
    background: #ffcc00;
  }
  50% {
    background: #00cc66;
  }
  75% {
    background: #0066cc;
  }
  100% {
    background: #ff7f7f;
  }
}

@media (max-width: 768px) {
  .topbar {
    font-size: 1.2rem;
    padding: 10px 15px;
    flex-direction: column;
    align-items: flex-start;
  }

  .topbar div {
    width: 100%;
    justify-content: flex-start;
    gap: 10px;
  }

  .topbar a {
    font-size: 0.9rem;
    padding: 6px 10px;
  }

  .container {
    margin-top: 120px;
    padding: 10px;
  }

  h1 {
    font-size: 2rem;
  }

  p {
    font-size: 1.2rem;
  }

  .url-section {
    font-size: 1rem;
    padding: 10px;
  }

  button {
    font-size: 1rem;
    padding: 10px 20px;
  }
}

@media (max-width: 480px) {
  .topbar {
    font-size: 1rem;
    padding: 10px;
    flex-direction: column;
    align-items: flex-start;
  }

  .topbar div {
    width: 100%;
    flex-wrap: wrap;
    gap: 10px;
  }

  .container {
    padding: 5px;
  }

  h1 {
    font-size: 1.5rem;
  }

  p {
    font-size: 1rem;
  }

  .url-section {
    font-size: 0.9rem;
    padding: 8px;
  }

  button {
    font-size: 0.9rem;
    padding: 8px 15px;
  }
}

.logo-container {
  display: flex;
  align-items: center;
  gap: 10px;
}

.logo {
  width: 40px;
  height: 40px;
  object-fit: contain;
  filter: invert(1) grayscale(100%) brightness(200%);
}

.header-container {
  display: flex;
  align-items: center;
  gap: 10px;
}

.header-logo-black {
  width: 70px; 
  height: 70px;
  object-fit: contain;
  filter: invert(18%) sepia(50%) saturate(440%) hue-rotate(115deg) brightness(90%) contrast(85%);
}